<template>
    <div class="blog-wrapper">
        <Section fullWidth="true">
            <div class="row m-t-100">
                <div class="col" style="flex:1;">
                    <h1>Blog</h1>
                </div>
                <div class="col" style="flex:2;">
                    <p>
                        Learn how artificial intelligence is augmenting or
                        automating human decision making around the world.
                        Ultralytecs is the foundational infrastructure that
                        powers all these applications within businesses big and
                        small.
                    </p>
                </div>
            </div>
        </Section>
        <Section v-if="featurePost">
            <FeaturePost :post="featurePost" />
        </Section>
        <Section>
            <div class="category-selector">
                <div
                    @click="changeCategory"
                    :data-category="category"
                    v-for="(category, index) in postCategories"
                    :key="'category-' + index"
                    class="category"
                    :class="(index == 0) ? 'selected' : ''"
                >
                    {{ category }}
                </div>
            </div>
            <PostBlock :category="selectedCategory" :columns="2"/>
        </Section>
    </div>
</template>
<script>
import LayoutMixin from "@/mixins/LayoutMixin"; // Contains structural components
import FeaturePost from "@/components/FeaturePost";
import PostBlock from "@/components/PostBlock";

export default {
    mixins: [LayoutMixin],
    // Lifecycle Hooks
    mounted() {
        this.fetchFeaturePost();
        this.fetchPostCategories();
    },
    components: {
        FeaturePost,
        PostBlock,
    },
    data() {
        return {
            featurePost: undefined,
            postCategories: undefined,
            selectedCategory: "all",
        };
    },
    methods: {
        changeCategory(e) {
            let category = e.currentTarget.dataset.category;
            this.selectedCategory = category;

            let preSelected = document.querySelector(".category.selected");

            if (preSelected) {
                preSelected.classList.remove("selected");
            }
            e.currentTarget.classList.add("selected");
        },
        fetchPostCategories() {
            console.log("[API CALL] Fetch Pricing Plans");
            let dummyAPIResponse = [
                "All",
                "Product",
                "Customers",
                "Engineering",
            ];
            this.postCategories = dummyAPIResponse;
        },
        fetchFeaturePost() {
            console.log("[API CALL] Fetch Pricing Plans");
            let dummyAPIResponse = {
                image: "https://picsum.photos/1000/417",
                readTime: "3 Min Read",
                title: "Livestock Monitoring",
                summary:
                    "Understanding animal health and environmental changes is critical to sustaining a healthy production line on a dairy farm. Companies use Labelbox to help analyze data from surveillance camera footage in order to provide the animals with the health and care needed. This makes the animals happier and allows the farms to be more productive.",
            };
            this.featurePost = dummyAPIResponse;
        },
    },
};
</script>

<style lang="scss" scoped>
.category-selector {
    display: flex;
    margin-bottom:1em;
    .category {
        text-transform: uppercase;
        padding: 0.5em 2em;
        cursor: pointer;
        color: #868f9b;
        background-color: #f9fafe;

        &.selected {
            background-color: $primaryColor;
            color: $whiteColor;
        }
    }
}
</style>
