import { mapGetters, mapActions } from "vuex";
export default {
    // Variables
    computed: {
        // Vuex
        ...mapGetters("cms", ["nav_menu"]),
    },
    methods: {
        // Vuex
        ...mapActions("cms", ["fetchNavigation"]),
        loadNavigation(menuId){
            // Check if state is set, if not fetch
            if(!this.nav_menu || !this.nav_menu[menuId]){
                this.fetchNavigation(menuId);
            }
        }
    },
};
