<template>
    <div class="section" :class="classOptions" :style="styleOptions">
        <div class="inner-section">
            <slot />
        </div>
    </div>
</template>
<script>
export default {
    computed: {
        classOptions() {
            let classes = "";
            if (this.fullHeight == "true") {
                classes += " full-height ";
            }
            if (this.fullWidth == "true") {
                classes += " full-width ";
            }
            return classes;
        },
        styleOptions() {
            let styles = "";
            if (this.overlay && this.bgImage) {
                styles +=
                    "background:linear-gradient(0deg, rgba(" +
                    this.overlay +
                    "), rgba(" +
                    this.overlay +
                    ")), url('" +
                    this.bgImage +
                    "');";
            } else if (this.bgImage) {
                console.log("image but no overlay ", this.overlay);
                styles += 'background-image:url("' + this.bgImage + '");';
            }
            if (this.bgColor) {
                styles += "background-color:" + this.bgColor + ";";
            }
            return styles;
        },
    },
    props: ["bgColor", "bgImage", "overlay", "fullHeight", "fullWidth"], //bgColor: hexcode, bgImage: url, overlay: rgba, fullHeight/fullWidth: boolean
};
</script>

<style lang="scss" scoped>
.section {
    .inner-section {
        display: flex;
        flex-direction: column;
        height: 100%;
        width: 100%;

        > .row:nth-child(1) {
            height: 100%;
        }
    }

    &.full-width {
        margin: 0 (-$contentPaddingMobile);

        .inner-section {
            padding: $contentPaddingMobile;
        }
    }

    +.section{
            margin-top:5em;
        }
}

@media screen and (min-width: $desktopBreak) {
    .section {
        &.full-height {
            height: 100vh;
            max-height: 67.5em; // 1080px
            background-position: center center !important;
            background-size: cover !important;
        }

        &.full-width {
            margin: 0 (-$contentPadding);

            .inner-section {
                padding: $contentPadding;
            }
        }

        +.section{
            margin-top:10em;
        }
    }
}
</style>
