<template>
<div class="tmp-wrapper">
    <div ref="container"></div>
</div>
</template>
<script>
import lottieWeb from "lottie-web";

export default {
    // Lifecycle Hooks
    mounted() {
        if (this.path) {
            this.loadAnimation();
        }
    },
    computed: {
        lottieConfig() {
            let config = {
                container: this.$refs.container,
                path: this.path,
                renderer: "svg",
            };
            if (this.loop) {
                config.loop = this.loop == "true" ? true : false;
            }
            if (this.autoPlay) {
                config.autoplay = this.autoPlay == "true" ? true : false;
            }
            if (this.name) {
                config.name = this.name;
            }
            if (this.segment){
                config.initialSegment = [this.segment.start, this.segment.end];
            }
            return config;
        },
    },
    data() {
        return {
            animation: undefined,
        };
    },
    props: ["path", "loop", "autoPlay", "name", "segment"],
    methods: {
        loadAnimation() {
            this.animation = lottieWeb.loadAnimation(this.lottieConfig);
        },
    },
};
</script>

<style lang="scss" scoped></style>
