export default{
    computed:{
        isMobile(){
            return this.viewportWidth() < this.tabletBreak;
        },
        isTablet(){
            let vw = this.viewportWidth();
            return vw > this.tabletBreak && vw < this.desktopBreak;
        },
        isDesktop(){
            return this.viewportWidth() > this.desktopBreak;
        },
        breakpoint(){
            if (this.isMobile){
                return 'mobile';
            }else if (this.isTablet){
                return 'tablet';
            }else if (this.isDesktop){
                return 'desktop';
            }else{
                return 'unknown';
            }
        }
    },
    data(){
        return{
            mobileBreak:300, // Not used, instead check if smaller than tablet break
            tabletBreak:768,
            desktopBreak:1024,
        }
    },
    methods:{
        screenWidth(){
            return screen.width || window.screen.width || window.innerWidth;
        },
        viewportWidth(){
            return window.innerWidth;
        }
    }
}