/**
 * Store for public facing website
 * To be used to implement basic CMS
 */
export default {
    namespaced: true,
    state: {
        nav_menu: [],
    },
    getters: {
        nav_menu(state) {
            return state.nav_menu;
        },
    },
    mutations: {
        ADD_NAV_MENU(state, {id, items}) {
            if (!state.nav_menu[id]){
                state.nav_menu.push(id); // Create id if not set
            }
            state.nav_menu[id] = items; // Overwrite existing nav
        },
    },

    actions: {
        fetchNavigation({ commit }, menuId) {
            // Dummy function : To connect to DB and fetch navigation via API
            let dummyAPIResponse;
            if (menuId == "primary") {
                dummyAPIResponse = [
                    {
                        text: "Blog",
                        link: "/blog/technology",
                    },
                    {
                        text: "Pricing",
                        link: "/pricing",
                    },
                    {
                        text: "Solutions",
                        link: "/blog/solutions",
                    },
                    {
                        text: "About Us",
                        link: "/about-us",
                    },
                    {
                        text: "Contact",
                        link: "/contact",
                    },
                ];
            }else if(menuId == "secondary"){
                dummyAPIResponse = [
                    {
                        text: "FAQ",
                        link: "/frequently-asked-questions",
                    },
                    {
                        text: "Terms of Use",
                        link: "/terms-of-use",
                    },
                    {
                        text: "Privacy Policy",
                        link: "/privacy-policy",
                    },
                ];
            }else if(menuId == "social"){
                dummyAPIResponse = [
                    {
                        text: '<i class="fab fa-youtube"></i>',
                        link: "#",
                    },
                    {
                        text: '<i class="fab fa-facebook-f"></i>',
                        link: "#",
                    },
                    {
                        text: '<i class="fab fa-instagram"></i>',
                        link: "#",
                    },
                    {
                        text: '<i class="fab fa-twitter"></i>',
                        link: "#",
                    },
                    {
                        text: '<i class="fab fa-linkedin-in"></i>',
                        link: "#",
                    },
                ];
            }else{
                console.log("[API CALL] Invalid Menu Id : Only primary && secondary are implemented");
                return;
            }
            console.log('[API CALL] Fetch Nav Menu : ', menuId);
            
            // let menu = { [menuId] : dummyAPIResponse };
            commit("ADD_NAV_MENU", {id:menuId, items:dummyAPIResponse});
        },
    },
};
