import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
    /**
     * -------------------------
     * PUBLIC FACING PAGES
     * -------------------------
     */
    {
        path: "/",
        name: "home",
        component: require("@/components/Pages/Home/index").default,
    },
    {
        path: "/about-us",
        name: "about",
        component: require("@/components/Pages/About/index").default,
    },
    {
        // There are two blog categories : blog && solutions
        path: "/blog/:category",
        name: "blog",
        component: require("@/components/Pages/Blog/index").default,
    },
    {
        path: "/contact",
        name: "contact",
        component: require("@/components/Pages/Contact/index").default,
    },
    {
        path: "/pricing",
        name: "pricing",
        component: require("@/components/Pages/Pricing/index").default,
    },
    {
        path: "/opportunities",
        name: "opportunities",
        component: require("@/components/Pages/Opportunities/index").default,
    },
];

const router = new VueRouter({
    mode: "history", // Beautifies URL but requires server config for direct URL access
    base: process.env.BASE_URL,
    routes,
    scrollBehavior() {
        return { x: 0, y: 0 }; // Always scroll to top of page on navigation
    }
});

export default router;
